.footer{
  padding: 40px 0;
  background-color: var(--dark-color);

  &__logo{
    display: inline-block;
    margin-bottom: 5px;
  }

  &__container{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__copy{
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--light-color);
  }
}
