.form{
  display: flex;
  flex-direction: column;
  width: 100%;

  &__label{
    margin-bottom: 10px;
    width: 100%;
  }

  &__input{
    border-radius: 5px;
    width: 100%;
    height: 64px;
    padding: 0 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);

    &::placeholder{
      color: rgba(40, 41, 68, 0.7);
    }
  }
  &__btn{
    padding: 20px 30px;
  }
}
